import React, { useState, useRef } from "react";
import "./NetflixOnly.scss";
import BtnSlideOnly from "../BtnSlide/BtnSlideOnly";
import onlyData from "../../assets/onlyData";

export default function NetflixOnly() {
  const ref = useRef();

  const [slideIndex, setSlideIndex] = useState(1);

  const slideRight = () => {
    if (slideIndex < 2) {
      setSlideIndex(2);
      ref.current.style.transform = "translateX(-100%)";
    }
  };
  const slideLeft = () => {
    if (slideIndex > 1) {
      setSlideIndex(1);
      ref.current.style.transform = "translateX(0)";
    }
  };

  return (
    <div className="container-only">
      <h2>
        Seulement sur Netflix
        <span>Tout explorer</span>
        <span>
          <i className="fas fa-angle-right"></i>
        </span>
      </h2>

      <div className="traits">
        <div
          className={slideIndex === 1 ? "trait trait-active" : "trait"}
        ></div>
        <div
          className={slideIndex === 2 ? "trait trait-active" : "trait"}
        ></div>
      </div>

      {slideIndex === 2 ? (
        <BtnSlideOnly slide={slideLeft} direction="left" />
      ) : (
        <BtnSlideOnly slide={slideRight} direction="right" />
      )}
      <div className="container-card" ref={ref}>
        {/* background */}
        {onlyData.map((item, id) => {
          return (
            <div key={id} className="card-only">
              <img className="bg-card" src={item.img} alt="image série" />

              <div className="card-content">
                {/* buttons */}
                <div className="btns-only">
                  <button>
                    <i className="fas fa-play"></i>
                  </button>
                  <button>
                    <i className="fas fa-plus"></i>
                  </button>
                  <button>
                    <i className="fas fa-angle-down"></i>
                  </button>
                </div>
                <h3 className="title-only">{item.title}</h3>
                {/* info */}
                <div className="infos-only">
                  <span className="age-limit">{item.ageMax}</span>
                  <span className="duration">{item.duration}</span>
                </div>
                {/* category */}
                <div className="category-only">
                  {item.category[0]}
                  <i className="fas fa-circle"></i> {item.category[1]}
                  <i className="fas fa-circle"></i> {item.category[2]}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
