import React, { useState, useRef } from "react";
import "./MyList.scss";
import BtnSlideList from "../BtnSlide/BtnSlideList";
import listData from "../../assets/listData";

export default function MyList() {
  const ref = useRef();

  const [slideIndex, setSlideIndex] = useState(1);

  const slideRight = () => {
    if (slideIndex < 2) {
      setSlideIndex(2);
      ref.current.style.transform = "translateX(-100%)";
    }
  };
  const slideLeft = () => {
    if (slideIndex > 1) {
      setSlideIndex(1);
      ref.current.style.transform = "translateX(0)";
    }
  };

  return (
    <div className="container-list">
      <h2>
        Ma liste
        <span>Tout explorer</span>
        <span>
          <i className="fas fa-angle-right"></i>
        </span>
      </h2>

      <div className="traits">
        <div
          className={slideIndex === 1 ? "trait trait-active" : "trait"}
        ></div>
        <div
          className={slideIndex === 2 ? "trait trait-active" : "trait"}
        ></div>
      </div>

      {slideIndex === 2 ? (
        <BtnSlideList slide={slideLeft} direction="left" />
      ) : (
        <BtnSlideList slide={slideRight} direction="right" />
      )}
      <div className="list" ref={ref}>
        {listData.map((item, id) => {
          return (
            <div key={id} className="items">
              <img src={item.img} className="item-img"></img>
              <div className="items-info">
                <div className="item item-buttons">
                  <button>
                    <i className="fas fa-play"></i>
                  </button>
                  <button>
                    <i className="fas fa-check"></i>
                  </button>
                  <button>
                    <i className="far fa-thumbs-up"></i>
                  </button>
                  <button>
                    <i className="far fa-thumbs-down"></i>
                  </button>
                  <button>
                    <i className="fas fa-angle-down"></i>
                  </button>
                </div>
                <div className="item item-season">
                  <span className="recomandation">{item.reco}</span>
                  <span className="age-limit">{item.ageMax}</span>
                  <span className="duration">{item.duration}</span>
                  <span className="definition">HD</span>
                </div>
                <div className="item item-category">
                  {item.category[0]} <i className="fas fa-circle"></i>{" "}
                  {item.category[1]}
                  {item.category[2] && <i className="fas fa-circle"></i>}
                  {item.category[2] && item.category[2]}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
